import {
	OrderDetail,
	WaitPaying,
	RefundPay,
	SureGood
} from '../../common/api.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	data() {
		return {
			oid: 0,
			detData: {},
			goodsList: [],
			refundData:{
				order_id:0,
				remark:""
			}
		}
	},
	components: {
		uniPopup,
	},
	onLoad(option) {
		this.oid = option.oid;
		this.pageInit();
	},
	methods: {
		pageInit() {
			let d = {
				order_id: this.oid
			};
			OrderDetail(d).then(res => {
				if (res.status) {
					this.detData = res.data;
					let a = [...this.detData.ordergoods];
					a.forEach((e) => {
						e.gg = JSON.parse(e.goods_snapshot)
					})
					this.goodsList = [...a];
					console.log(this.goodsList)
				}
			})
		},
		fkClick(){
			let d = {order_id:this.detData.id};
			let _this = this;
			WaitPaying(d).then(res=>{
				if(res.status){
					let cog = res.config;
					wx.requestPayment({
					  timeStamp: cog.timeStamp,
					  nonceStr: cog.nonceStr,
					  package: cog.package,
					  signType: cog.signType,
					  paySign: cog.paySign,
					  success (res) { 
						  uni.showToast({
						  	icon: "success",
						  	title: '支付成功'
						  });
						  _this.pageInit();
					  },
					  fail (res) { }
					})
				}
			})
		},
		tkClick(){
			this.$refs.popup.open();
		},
		refundClick(){
			this.refundData.order_id = this.detData.id;
			RefundPay(this.refundData).then(res=>{
				if(res.status){
					this.pageInit();
					this.$refs.popup.close();
				}
			})
		},
		shClick(){
			let d = {order_id:this.detData.id};
			let _this = this;
			SureGood(d).then(res=>{
				if(res.status){
					uni.showToast({
						icon: "success",
						title: '收货成功',
						success:function(){
							_this.pageInit();
						}
					});
				}
			})
		}
	}
}
